export default {
  title: 'Design System/Avatar Component',
  // More on argTypes: https://storybook.js.org/docs/html/api/argtypes
  argTypes: {
    hasCaption: { control: 'boolean'},
    name: { control: 'text'},
    date: { control: 'text'},
  },
  args: {
    hasCaption: false,
    name: '',
    alt: '',
    url: ''
  }
};

